"use client";
import { useState } from "react";

export default function Faq() {
  const [activeIndex, setActiveIndex] = useState<number | null>(0);
  const faqs = [
    { title: "¿Qué documentos necesito?", content: "Identificación oficial, formularios fiscales, recibos." },
    { title: "¿Cómo inicio una corporación?", content: "Elige nombre y tipo de entidad. Nosotros ayudamos con el resto." },
    { title: "¿Qué servicios contables ofrecen?", content: "Gestión diaria, estados financieros y nómina." }
  ];

  return (
    <div className="faq-style-one-area relative default-padding" id="faq" style={{ backgroundImage: "url(/assets/img/shape/32.png)" }}>
      <div className="container">
        <div className="row align-center">
          <div className="col-lg-6">
            <div className="faq-style-one">
              <h4 className="sub-heading">FAQ</h4>
              <h2 className="title mb-30">Aclaramos dudas</h2>
              <div className="accordion" id="faqAccordion">
                {faqs.map((item, index) => (
                  <div className="accordion-item" key={index}>
                    <h2 className="accordion-header">
                      <button className={`accordion-button ${activeIndex === index ? '' : 'collapsed'}`} type="button" onClick={() => setActiveIndex(activeIndex === index ? null : index)}>
                        {item.title}
                      </button>
                    </h2>
                    <div className={`accordion-collapse collapse ${activeIndex === index ? 'show' : ''}`}>
                      <div className="accordion-body"><p>{item.content}</p></div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
          <div className="col-lg-5 offset-lg-1 mt-md-50 mt-xs-30">
            <div className="faq-thumb"><img src="/assets/img/illustration/6.png" alt="FAQ" /></div>
          </div>
        </div>
      </div>
    </div>
  );
}